.Contain-Template{
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 30px 30px;
    height: 450px;
    width: 500px;
    background-color: #f4bc96;
}
.Title{
    width: 100%;
    font-size: 32px;
    font-family: URW Chancery L, cursive;
    font-weight: 700;
}
h1{
    margin: 0 0 ;
    padding: 0 0 ;
    line-height: 8px;
    margin-bottom: 5px;
    text-align: center;
    padding-bottom: 4px;
}
.Main-Order{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.ListOption{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-weight: 600;
}

.Topping-Name{
    width: 22%;
    text-align: left;
    font-weight: 600;
}

.Salad-Row,
.Cheese-Row,
.Meat-Row,
.Bacon-Row{
    display: flex;
    flex-direction: row;
    margin: auto;
    width: 100%;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    line-height: 30px;
    font-size: 20px;
    transform: translateX(-10px);
}
.Unit-Salad,
.Unit-Cheese,
.Unit-Meat,
.Unit-Bacon{
    width: 33%;
    box-sizing: border-box;
    text-align: left;
    transform: translateX(-60px);
}
.Input-Salad,
.Input-Cheese,
.Input-Meat,
.Input-Bacon{
    width: 33.3%;
    height: 22px;
    font-size: 20px;
    text-align: center;
}
.Unit-Salad button,
.Unit-Cheese button,
.Unit-Meat button,
.Unit-Bacon button{
    width: 20%;
    box-sizing: border-box;
    min-height: 28px;
    color: rgba(0,0,0,.8);
    font-size: 20px;
    background: white;
    border: 1px solid black ;
    cursor: pointer;
}

.Price-Salad,
.Price-Cheese,
.Price-Meat,
.Price-Bacon{
    width: 42px;
    box-sizing: border-box;
    transform: translateX(-70px);
}
span{
    text-align: center;
}
.Order-And-Reset-Contain{
    height: 80px;
    width: 100%;
    margin-top: 20px;
    display: flex; 
    gap: 40px;
    font-size: 20px;
    font-weight: 700;
    justify-content: center;
    align-items: center;
}
.Order-Btn,.Reset-Btn{
    font-weight: 600;
    background-color: #F37335;
    color: white;
    height: 100%;
    cursor: pointer;
}
.Total-Payment{
    width: 150px;
    transform: translateX(-25px);
}

